const ActivityChart = ({ report }) => {
  if (!report) return <></>
  const { response, test_task, tech_interview, offer } = report
  const classes = useStyles()

  const detail = (
    <ul className={classes.list}>
      <li className={classes.list__item}>
        <span className={classes.list__number}>{response}</span>
        <p className={classes.list__title}>Отклики на вакансии</p>
      </li>
      <li className={classes.list__item}>
        <span className={classes.list__number}>{test_task}</span>
        <p className={classes.list__title}>Тестовые задания</p>
      </li>
      <li className={classes.list__item}>
        <span className={classes.list__number}>{tech_interview}</span>
        <p className={classes.list__title}>Технические собеседования</p>
      </li>
      <li className={classes.list__item}>
        <span className={classes.list__number}>{offer}</span>
        <p className={classes.list__title}>Офферы</p>
      </li>
    </ul>
  )

  return (
    <Tooltip
      title={detail}
      classes={{ tooltip: classes.tooltip }}
      enterDelay={500}
      leaveDelay={200}
      placement="right-end"
    >
      <span className={classes.info}>{`${response} / ${test_task} / ${tech_interview} / ${offer}`}</span>
    </Tooltip>
  )
}

export default ActivityChart
