import * as React from 'react'
import Tooltip from '@mui/material/Tooltip'
import useStyles from './CommentToolTip.style'

function CommentToolTip({ detail, comment, interviewDate, children, ...props }) {
  const classes = useStyles()
  return (
    <Tooltip title={detail} {...props} classes={{ tooltip: classes.tooltip }} enterDelay={500} leaveDelay={200}>
      <span className={classes.comment}>{detail}</span>
    </Tooltip>
  )
}

export default CommentToolTip
