import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import { IconButton } from '@material-ui/core'
import Tooltip from '@mui/material/Tooltip'
import { gridDateComparator } from '@mui/x-data-grid'
import StatusSelect from '../components/Sub/DataTable/StatusSelect/StatusSelect'
import StudentInfo from '../components/Sub/DataTable/StudentInfo/StudentInfo'
import CourseInfo from '../components/Sub/DataTable/CourseInfo/CourseInfo'
import CommentToolTip from '../components/Sub/DataTable/CommentToolTip/CommentToolTip'
import ActivityChart from '../components/Sub/DataTable/ActivityChart/ActivityChart'
import { formatDayMonthYear, formatRusStyle, getDurationInYearWeekDay } from './dates'
import { IconCharts, IconDiary, IconFeed } from '../assets/Icons'

export const getApplicationColumns = (patchStatus, courses, applicationStatuses) => [
  { field: 'id', headerName: 'ID', width: 50 },
  {
    field: 'date',
    type: 'dateTime',
    headerName: 'Дата',
    width: 110,
    valueFormatter: (params) => formatDayMonthYear(params.value),
  },
  {
    field: 'studentInfo',
    headerName: 'Студент',
    width: 195,
    renderCell: (params) => (
      <StudentInfo firstName={params.row?.first_name} lastName={params.row?.last_name} email={params.row?.email} />
    ),
  },
  {
    field: 'course',
    headerName: 'Курс',
    width: 130,
    renderCell: (params) => <CourseInfo course={courses?.find((c) => c.id === params.value)?.name} />,
  },
  {
    field: 'cohort_number',
    headerName: 'Поток',
    type: 'number',
    align: 'left',
    width: 60,
    sortable: false,
  },
  { field: 'type', headerName: 'Тип', width: 120 },
  {
    field: 'status',
    headerName: 'Статус',
    width: 145,
    renderCell: (params) => (
      <StatusSelect
        activeStatus={params.value}
        onPatch={patchStatus}
        applicationId={params.row?.id}
        statuses={applicationStatuses}
        width={130}
      />
    ),
  },
  { field: 'staff', headerName: 'Ответственный', width: 195 },
  {
    field: 'details',
    headerName: 'Детали',
    sortable: false,
    renderHeader: () => (
      <div style={{ display: 'flex', justifyContent: 'space-between', width: '100%', position: 'relative' }}>
        <span>Детали</span>
      </div>
    ),
    width: 190,
    renderCell: (params) => <CommentToolTip detail={params.row?.detail} />,
  },
]

const sortAccelerationDateComparator = (v1, v2) => gridDateComparator(new Date(v1), new Date(v2))

export const getStudentsColumns = (
  patchStatus,
  courses,
  handleFeedClick,
  handleDiaryClick,
  handleMetricsClick,
  studentsStatuses,
) => [
  { field: 'id', headerName: 'ID', width: 50 },
  {
    field: 'studentInfo',
    headerName: 'Студент',
    width: 186,
    renderCell: (params) => (
      <StudentInfo firstName={params.row?.first_name} lastName={params.row?.last_name} email={params.row?.email} />
    ),
  },
  {
    field: 'course',
    headerName: 'Курс',
    width: 160,
    renderCell: (params) => <CourseInfo course={courses?.find((c) => c.id === params.value)?.name} />,
  },
  {
    field: 'cohort_number',
    headerName: 'Поток',
    type: 'number',
    align: 'left',
    width: 60,
    sortable: false,
  },

  {
    field: 'live_time',
    headerName: 'Время в тр-ве',
    width: 120,
    valueFormatter: (params) => getDurationInYearWeekDay(params?.value),
    // sortComparator: sortAccelerationDateComparator,
  },
  {
    field: 'status',
    headerName: 'Статус',
    width: 220,
    renderCell: (params) => (
      <StatusSelect
        onPatch={patchStatus}
        applicationId={params.row?.id}
        activeStatus={params.value}
        statuses={studentsStatuses}
        width={188}
      />
    ),
  },
  {
    field: 'student_stats',
    headerName: 'Общая воронка',
    width: 202,
    sortable: false,
    renderCell: (params) => <ActivityChart report={params.row?.student_stats} isUser />,
  },
  {
    field: '_',
    headerName: ' ',
    width: 202,
    sortable: false,
    disableReorder: true,
    renderHeader: () => (
      <div style={{ display: 'flex', justifyContent: 'space-between', width: '100%', position: 'relative' }}>
        <span>&nbsp;</span>
      </div>
    ),
    renderCell: (params) => (
      <ul
        style={{
          padding: 0,
          margin: '0 0 0 17px',
          listStyle: 'none',
          display: 'flex',
        }}
      >
        <li>
          <ButtonHover
            params={params}
            icon={<IconCharts />}
            title="Открыть метрики"
            onClick={() =>
              handleMetricsClick({
                studentName: `${params.row?.first_name} ${params.row?.last_name}`,
                studentId: params.row?.id,
                profile: params.row,
              })
            }
          />
        </li>
        <li>
          <ButtonHover
            params={params}
            icon={<IconDiary />}
            title="Доска студента"
            onClick={() =>
              handleDiaryClick({
                studentName: `${params.row?.first_name} ${params.row?.last_name}`,
                studentId: params.row?.id,
              })
            }
          />
        </li>
        <li>
          <ButtonHover
            params={params}
            icon={<IconFeed />}
            title="Лента студента"
            onClick={() =>
              handleFeedClick({
                studentName: `${params.row?.first_name} ${params.row?.last_name}`,
                studentId: params.row?.id,
              })
            }
          />
        </li>
      </ul>
    ),
  },
]

