import api from '../../../../api/api'
import { API_STUDENTS_LIST_ENDPOINT } from '../../../../utils/constants'

const getStudents = async (setIsLoading, setStudents, filters, setRowsCount) => {
  try {
    setIsLoading(true)
    const response = await api.get(API_STUDENTS_LIST_ENDPOINT, {
      params: { ...filters, profile_id: filters?.studentId, studentId: undefined },
    })
    const rawStudents = Object.values(response.data?.results)
    const transformedStudents = rawStudents.map((s) => ({ ...s, course: s?.course?.id }))
    setStudents(transformedStudents)
    setRowsCount(response.data?.count)
    return Promise.resolve(response.data)
  } catch (e) {
    return Promise.reject(e.response)
  } finally {
    setIsLoading(false)
  }
}

export default getStudents
